          <button @click="gots" class="menu_ts" :class="{'btnBackground':$route.name == 'About'}">{{$t('shop.dining_here')}}</button>
          <button @click="gowm" class="menu_wm" :class="{btnBackground:$route.name == 'Wm' }">{{$t('shop.take_away')}}</button>
        </div>

      </div>

