        requirementShow: false,
        tipsShow: false,
        changeLangShow: false
        // cartShow:false
      }
    },

    methods: {
      handleClick(tab, event) {
        console.log(tab, event)
      },
      ShowchangeLang() {
        this.changeLangShow = true
      },
      goHome() {
        this.$router.push('/orderlist')
      },
      download() {
        this.tipsShow = true
      },
      shopcart() {
        // console.log(this.$refs.cart.$el)
        this.$store.state.cartShow = !this.$store.state.cartShow
      },
      getToken2() {


      },
      accounts() {
        if (this.$store.state.totalPrice != 0) {
          // console.log(this.startTime,this.endTime,this.isOpen)

          let nowTime = new Date().getHours()
          console.log(this.startTime,this.endTime)
          if (nowTime < this.startTime || nowTime > this.endTime || !this.isOpen) {
            alert(this.$t('shop.shop_closed'))
            return
          }


          if (this.$route.name == 'About') {
            this.$store.commit('updataDiningStyle', '堂食')
          }
          if (this.$route.name == 'Wm') {
            this.$store.commit('updataDiningStyle', '自提')
          }

          if (!localStorage.getItem('userInfo')) {
            console.log('要先登录')
            this.$router.push('/login')
          } else {
            console.log('进入订单确认页面')
            this.$router.push('/orderconfirm')
          }

        }

      },
      gots() {
        if (this.$route.name == 'Wm') {
          if (confirm(this.$t('shop.switch_dinning_way_warnning'))) {
            this.$store.commit('clearListNum')
            this.$store.commit('clearCart')
            this.$router.push('/about')
          }
        } else {
          console.log('已在自提页面')
        }
      },
      gowm() {
        if (this.$route.name == 'About') {
          if (confirm(this.$t('shop.switch_dinning_way_warnning'))) {
            this.$store.commit('clearListNum')
            this.$store.commit('clearCart')
            this.$router.push('/Wm')
          }
        } else {
          console.log('已在堂食页面')
        }

      },
      /**获取地图定位*/
      // getLocation() {

      //   let geolocation = location.initMap("map-container"); //定位
      //   AMap.event.addListener(geolocation, "complete", result => {
      //       console.log(result)
      //   });

      // },




    },

    mounted() {

      //    messaging.getToken({vapidKey: "BEM3lCqGpb9BtPhjHBOUy1NnIBujRzRibNPOiUmhpPTESFZ3-XdK6pPj8QcCM1U9uWXeN7ast6LnvVys6osioY8"}).then((currentToken) => {
      //   if (currentToken) {
      //     console.log(currentToken)
      //     // Send the token to your server and update the UI if necessary
      //     // ...
      //   } else {
      //     // Show permission request UI
      //     console.log('No registration token available. Request permission to generate one.');
      //     // ...
      //   }
      // }).catch((err) => {
      //   console.log('An error occurred while retrieving token. ', err);
      //   // ...
      // });
      if (this.$store.state.aboutlists.length == 0 || this.$store.state.category.length == 0) {
        console.log('首次加载，发送请求')
        //this.getQueryString('qrcode_id'))
        // }
        console.log(this.$route)
        if (this.$route.query.qrcode_id) {
          localStorage.setItem('qrcodeId', this.$route.query.qrcode_id)
        }
        let qrcodeId = localStorage.getItem('qrcodeId')
        axios(
            `https://jzk80vaqs7.execute-api.ap-northeast-1.amazonaws.com/dev/query_qrcode_by_id/v1?qrcode_id=${qrcodeId}`
          )
          .then(res => {
            console.log(res.data)
            this.$store.commit('setTableId', res.data.table_id)
            this.$store.dispatch('getListInfo', res.data.shop_id)
          }).catch(err => {
            console.log(err.response)
            alert(this.$t('error.unknownErr'))
          })


      } else {

        console.log('从缓存获取信息，不用再发送请求了')
      }

    },

    components: {
      cart,
      requirement,
      selectsize,
      tip,
      changelang,
    },
    computed: {
      startTime: {
        get() {
          return this.$store.state.shopInfo.do_business_start_minute / 60
        },
      },
      endTime: {
        get() {
          return this.$store.state.shopInfo.do_business_end_minute / 60
        }

      },
      isOpen: {
        get() {
          return this.$store.state.shopInfo.is_open
        }
      },
      tableId: {
        get() {

          return this.$store.state.tableId




        }
      }
    }
  }
</script>

