<template>
  <div class="outbox" v-if=" list.status==1 && (list.pack_base_price!=null&& $route.name=='Wm'  || $route.name=='About')">
    <div class="mealbox">
      <img :src="list.oss_url || list.set_photo_url" />
    </div>
    <label class="product"> {{list.name || list.set_name}} </label>
    <label class="countdown" v-if="list.set_time">限时{{list.set_time}}分钟</label>

    <!-- <label class="countdown" v-if="list.description">{{list.description}}</label> -->

    <label class="price" v-show="!isWm"> <span class="moneyIcon">¥</span>{{ list.house_base_price}}</label>


    <label class="price"   v-show="isWm "> <span class="moneyIcon">¥</span>{{list.pack_base_price}}</label>

     <label class="price1" v-if="list.canSelectSize" v-show="isWm && this.$store.state.cartShow == true" > <span class="moneyIcon">¥</span>{{list.pickPrice}}</label>
    <!-- <label class="price" > <span class="moneyIcon">¥</span>{{list.price | isUndefine}}</label> -->
    <!-- <div class="carbox">
      <ul id="car">
        <li><button v-on:click="subtract()">-</button></li>
        <li><input type="text" value="0" size="1" v-model="list.num" /></li>
        <li><button v-on:click="add()">+</button></li>
      </ul>
    </div> -->
    <el-input-number v-if="list.disabled!=undefined" :disabled="list.disabled" ref="number" class="number" v-model="currentNum"
      @change="handleChange" :min="0" :max="max"  ></el-input-number>
      <div @touchmove.prevent  class=" number_zz"></div>
    <el-input-number   v-if="list.disabled==undefined" :disabled="list.disabled" ref="number2" class="number" v-model="currentNum"
      @change="handleChange" :min="0" :max="100"    ></el-input-number>
    <!-- <el-input-number v-if="list.disabled!=undefined" ref="number" class="number" v-model="currentNum" @change="handleChange" :min="0" :max="100"></el-input-number> -->
    <selectsize v-if="list.size&&list.size.length>1" v-show="showSelection" :list="list" @hide="showSelection=!showSelection"></selectsize>
    <div @touchmove.prevent  @click="cancelSelection" v-show="showSelection" id="ZZ"></div>
  </div>

</template>

<script>
  import selectsize from './shop/SelectSize.vue'
  export default {
    name: "car",
    props: ['list'],

    methods: {
      cant(){
        console.log(111)
      },
      cancelSelection(){
          this.showSelection = false
          this.currentNum--
      },
      handleChange(currentValue, oldValue) {
       // console.log(currentValue, oldValue, this.currentNum)
        /***   这里是堂食页面 ***/
        if (!this.isWm) {
          // console.log(currentValue, oldValue)
          /*数量增加*/
          if (currentValue >= oldValue) {

            //只要不能选，默认添加
            if (!this.list.canSelectSize) {
              this.$store.commit('totalPrice', this.list.house_base_price)
              this.list.num = this.currentNum
              this.list.pickPrice = this.list.house_base_price
            }
            /*  这是列表控制购物车中的数量*/
            if (this.$store.state.cartShow == false) {

              if (this.list.canSelectSize) {
                console.log('这是可以选择的')
                //在面板控制添加
                this.showSelection = true

              } else {
              //  console.log(this.list.selectedSize)

                /*如果当前点击的是套餐*/
                if (this.list.buffet_id) {
                  console.log('购物车的数量改变的了')
                  this.$nextTick(() => {
                    this.updataMaxNum() //更新子商品最大选择数量
                    this.changeDisable() //将子商品变为可以选择
                  //  this.updataCartDetail()
                    this.updataBufferrDetail() //更新子商品的状态
                  })

                  /*更新到购物车*/
                  this.$store.commit('setcartInfo', this.list)
                } else {
                  this.$store.commit('setcartInfo', this.list)
                }

              }
            }
            /*  这是购物车增加列表中的数量*/
            else {
               if(this.list.canSelectSize){
                 this.list.num = this.currentNum

                this.$store.commit('totalPrice', this.list.pickPrice)
               }
              /*如果当前点击的是套餐*/
              if (this.list.buffet_id) {
                console.log('现在增加的套餐在列表中的数量')
                this.$nextTick(() => {
                  this.updataMaxNum() //更新子商品最大选择数量
                  this.updataBufferrDetail() //更新到商品列表
                  this.updataCartDetail()
                  this.$store.commit('updataBufferInfo', this.list) //更新到商品列表
                })


              } else {
                /*如果当前点击的是套餐的子商品*/
                if (this.list.disabled != undefined) {
                  console.log('从购物车增加套餐商品在列表')
                  this.$store.commit('updataBufferrDetail', this.list) //更新到子商品列表

                }

                else {


                  console.log('从购物车增加普通商品在列表')

                  this.$store.commit('setList', this.list)
                }
              }
            }

            this.$store.commit('totalGoods') //统计总的商品数量

            /*堂食数量减少*/
          } else {
           if (this.list.canSelectSize && this.$store.state.cartShow == false) {
                 alert(this.$t('shop.to_cancel'))
                 this.$nextTick(()=>{
                   this.currentNum++
                 })

                 return

           }
            this.$store.commit('totalPrice', -this.list.house_base_price)
            this.list.num = this.currentNum
            /*在列表中*/
            if (this.$store.state.cartShow == false) {
              /*点击的如果是套餐*/
              if (this.list.buffet_id && this.currentNum!=0 ) {
                console.log('购物车的数量改变的了')
                this.$nextTick(() => {
                  this.updataMaxNum() //更新子商品最大选择数量
                  this.updataCartDetail() //更新购物车中子商品的状态
                  this.updataBufferrDetail() //更新列表子商品的状态
                })
                /*更新到购物车*/
                this.$store.commit('setcartInfo', this.list)
              } else {
                this.$store.commit('setcartInfo', this.list)
              }
            }

            /*在购物车中*/
            else {



              if (this.list.buffet_id ) {
                console.log('现在减少的套餐在列表中的数量')
                this.updataMaxNum()
                this.$nextTick(() => {
                  this.updataBufferrDetail()
                  this.updataCartDetail()
                })

                this.$store.commit('updataBufferInfo', this.list)

              } else { //这是在购物车中减少
                if (this.list.disabled != undefined) {
                  // this.updataMaxNumber()
                  console.log('从购物车减少套餐商品在列表')
                  this.list.num = this.currentNum
                  this.$store.commit('updataBufferrDetail', this.list)

                } else {
                  console.log('从购物车减少普通商品在列表')
                  this.$store.commit('setList', this.list)
                }
              }
            }

            this.$store.commit('totalGoods')


          }

        }
        /***   这里是自提页面 ***/
        else {

          if (currentValue > oldValue) {
          //只要不能选，默认添加
            if (!this.list.canSelectSize) {
                 this.$store.commit('totalPrice', this.list.pack_base_price)
                this.list.num = this.currentNum
                this.list.pickPrice = this.list.pack_base_price
            }
            /*  这是列表控制购物车中的数量*/
            if (this.$store.state.cartShow == false) {

              if (this.list.canSelectSize) {
                console.log('这是可以选择的')
                this.showSelection = true
                this.list.num = this.currentNum
              } else {
                console.log(this.list.selectedSize)
                this.$store.commit('setcartInfo', this.list)
              }
            }
            /*  这是购物车增加列表中的数量*/
            else {
                  if(this.list.canSelectSize){

                       this.list.num = this.currentNum
                        this.$store.commit('totalPrice', this.list.pickPrice)

                      this.$store.commit('setList', this.list)
                  }else{
                    console.log('从购物车增加普通商品在列表')
                    this.$store.commit('setList', this.list)
                  }


            }

            this.$store.commit('totalGoods') //统计总的商品数量
            /*增加*/
            //   this.$store.commit('totalPrice', this.list.pack_base_price)
            //   this.$store.commit('totalGoods', 1)
            //   //添加一个Key=>num
            //   this.list.num = this.currentNum
            //   /*  这是控制购物车中的数量*/
            //   if (this.$store.state.cartShow == false) {
            //     this.$store.commit('setcartInfo', this.list)
            //   }
            //   /*  这是控制列表中的数量*/
            //   else {
            //     this.$store.commit('setList', this.list)
            //   }
            // }
            //只要不能选，默认添加


          }

          /*减少*/
          else {

            if (this.list.canSelectSize && this.$store.state.cartShow == false) {
                  alert(this.$t('shop.to_cancel'))
                  this.$nextTick(()=>{
                    this.currentNum++
                  })

                  return

            }
            //只要不能选，默认添加
            if (!this.list.canSelectSize) {
              this.$store.commit('totalPrice', -this.list.pack_base_price)
              this.list.num = this.currentNum
            }else{
              this.list.num = this.currentNum
              this.$store.commit('totalPrice', -this.list.pickPrice)
            }
            /*  这是列表控制购物车中的数量*/
            if (this.$store.state.cartShow == false) {

               if (this.list.canSelectSize) {
                 console.log('这是可以选择的')
                 this.list.num = this.currentNum
                 this.$store.commit('setcartInfo', this.list)
               } else {
                 console.log(this.list.selectedSize)
                 this.$store.commit('setcartInfo', this.list)
               }

            }
            /*  这是购物车减少列表中的数量*/
            else {

              console.log('从购物车减少普通商品在列表')

                this.$store.commit('setList', this.list)



            }

            this.$store.commit('totalGoods') //统计总的商品数量
            // this.$store.commit('totalPrice', -this.list.pack_base_price)
            // this.$store.commit('totalGoods', -1)
            // this.list.num = this.currentNum
            // /*  这是控制购物车中的数量*/
            // if (this.$store.state.cartShow == false) {

            //   this.$store.commit('setcartInfo', this.list)
            // }
            // /*  这是控制列表中的数量*/
            // else {
            //   this.$store.commit('setList', this.list)
            // }

          }

        }
      },
      updataBufferrDetail() {
        if (this.$store.state.bufferDetail.length != 0) {
          let temp = this.$store.state.bufferDetail.splice(0, this.$store.state.bufferDetail.length)
          this.$nextTick(() => {
            this.$store.state.bufferDetail = [...temp]
            console.log('套餐标签被点击')
          })

        }
      },
      // updataMaxNumber() {
      //   for (let i of this.$store.state.bufferDetail) {
      //     if (i.category_id == this.list.buffet_id) {
      //       i.max = this.list.num * 5

      //     }
      //   }
      // },
      updataCartDetail() {

        let temp = this.$store.state.cartInfo.splice(0, this.$store.state.cartInfo.length)
        this.$nextTick(() => {
          this.$store.state.cartInfo = [...temp]

          //  console.log('套餐标签被点击')
          console.log('更新购物车')
        })



      },
      removeFromCart(){
        for(let i of this.$store.state.cartInfo){
          if(i.num==0){
            /*执行了这个*/
            console.log('啊？')
            let index = this.$store.state.cartInfo.indexOf(i)
            this.$store.state.cartInfo.splice(index,1)
          }
        }
      },

      updataMaxNum() {
        for (let i of this.$store.state.bufferDetail) {
          // console.log('执行最大数量加5')
          if (i.category_id == this.list.buffet_id) {
            i.max = this.list.num * 5
            console.log(i.max, '列表更新后max')
            if (i.num > i.max) {
              console.log('现在这个的数量大于最大值')
              i.num = i.max
            }
          }

        }
        for (let i of this.$store.state.cartInfo) {
          if (i.category_id == this.list.buffet_id) {
            i.max = this.list.num * 5
            console.log(i.max, '购物车更新后max')
            if (i.num > i.max) {
              console.log('现在这个的数量大于最大值')
              i.num = i.max
            }
          }
        }
        //  this.updataCartDetail()
        // this.updataBufferrDetail()
        console.log('套餐最大数量被点击增加')
      },
      changeDisable() {
        for (let i of this.$store.state.bufferDetail) {
          if (i.category_id == this.list.buffet_id) {
            i.disabled = false
            console.log('子商品可以点击了')
          }

        }
      },

    },
    components: {
      selectsize
    },
    data() {
      return {
        count: 0,
        // num: 0 ,
        currentNum:this.list.num,
        category: [],
        isWm: false,
        isBuffet: false,
        max: this.list.max,
        showSelection: false
      };
    },
    computed:{

    },
    // beforeMount() {
    //   if(this.$store.state.bufferDetail.includes(this.list) ){
    //   //  this.$store.commit('setDisabled',true)

    //   }
    // },
    mounted() {
      //console.log(this.list.size.includes('house_price'))
      //this.category = this.list.size.split(',')
      // console.log(this.category)
      if (this.$route.name == 'About') {
        this.isWm = false
      }
      if (this.$route.name == 'Wm') {
        this.isWm = true
      }


      // console.log(this.$refs.number.value)
    },
    watch: {
      currentNum: {
        handler(newVal, oldValue) {
          /*在列表中控制套餐减少为0的时候应该修改全部数量为0*/

          if (newVal == 0  && this.$store.state.bufferInfo.includes(this.list)) {
                console.log('取消所有套餐的东西')
                if(!this.$store.state.cartShow){
                  for (let i of this.$store.state.bufferDetail) {
                   // console.log('这个执行了几次12312')
                    if (i.category_id == this.list.buffet_id ) {
                      console.log(i,'只有这几个会把num变成0')
                        i.disabled = true
                        i.num = 0
                          if(i.canSelectSize){
                           this.$store.state.nums[i.id+i.selectedSize+'buffet']=0

                          }

                            this.$store.commit('setcartInfo', i)
                          // this.updataCartDetail()

                    }
                  }

                }else{
                  for (let i of this.$store.state.cartInfo) {
                //    console.log(i.category_id,this.list.buffet_id)

                    if (i.category_id == this.list.buffet_id ) {

                      console.log('这个执行了几次')
                       //  let index = this.$store.state.cartInfo.indexOf(i)
                         setTimeout(()=>{
                           setTimeout(()=>{
                              this.$store.commit('setcartInfo', i)
                             /// this.updataCartDetail()
                           })
                         })

                        // console.log(index)
                        // this.$store.state.cartInfo.splice(index,1)
                    }
                  }
                }


            this.updataBufferrDetail()

               this.updataCartDetail()


         //

          }



          /*套餐数量减少，相应子商品最大值减少5*/
          // if (newVal < oldValue && this.$store.state.bufferInfo.includes(this.list) && oldValue != undefined) {
          //   this.updataMaxNum()
          //   this.updataCartDetail()
          //   this.updataBufferrDetail()
          //   console.log('套餐最大数量被点击减少')
          // }

          /*在购物车控制*/

          /*套餐增加*/
          // if (newVal > oldValue && this.$store.state.cartInfo.includes(this.list) && this.$store.state.cartShow) {


          //   for (let i of this.$store.state.bufferDetail) {

          //     if (i.category_id == this.list.buffet_id) {
          //       i.max = this.list.num * 5
          //       console.log(i.max, '列表增加后max')
          //     }
          //   }

          //   for (let i of this.$store.state.cartInfo) {
          //     if (i.category_id == this.list.buffet_id) {
          //       i.max = this.list.num * 5
          //       console.log(i.max, '购物车增加后max')
          //     }
          //   }

          //   this.updataCartDetail()
          //   this.updataBufferrDetail()

          //   console.log('增加')
          // }
          /*套餐减少*/
          // console.log(newVal,oldValue)
          // if (newVal < oldValue && this.$store.state.cartInfo.includes(this.list) && this.$store.state.cartShow) {

          //   // for (let i of this.$store.state.bufferDetail) {

          //   //   if (i.category_id == this.list.buffet_id) {
          //   //     i.max = this.list.num * 5
          //   //     console.log(i.max, '列表减少后max')
          //   //   }
          //   // }

          //   // for (let i of this.$store.state.cartInfo) {
          //   //   if (i.category_id == this.list.buffet_id) {
          //   //     i.max = this.list.num * 5
          //   //     console.log(i.max, '购物车减少后max')
          //   //   }
          //   // }


          //   // this.updataCartDetail()
          //   // this.updataBufferrDetail()
          //   // console.log(1)
          //   console.log('减少')
          // }


          /*  这是初始化的时候*/
          if (newVal == 0 && oldValue == undefined) {
            this.$nextTick(() => {
              if (this.$refs.number) {
                this.$refs.number.$refs.input.$refs.input.style.display = 'none'
              }
            })

          } else {
            this.$nextTick(() => {
              if (this.$refs.number) {
                this.$refs.number.$refs.input.$refs.input.style.display = 'block'
              }
            })
          }
          /*这是更新的时候*/
          if (newVal == 0) {
            this.$nextTick(() => {
              if (this.$refs.number) {
                this.$refs.number.$refs.input.$refs.input.style.display = 'none'
              }
            })
          }

          /*控制套餐的最大值*/
          if (newVal == 0 && oldValue == undefined) {
            this.$nextTick(() => {
              if (this.$refs.number2) {
                this.$refs.number2.$refs.input.$refs.input.style.display = 'none'
              }
            })

          } else {
            this.$nextTick(() => {
              if (this.$refs.number2) {
                this.$refs.number2.$refs.input.$refs.input.style.display = 'block'
              }
            })
          }
          /*这是更新的时候*/
          if (newVal == 0) {
            this.$nextTick(() => {
              if (this.$refs.number2) {
                this.$refs.number2.$refs.input.$refs.input.style.display = 'none'
              }
            })
          }
        },
        immediate: true
      }
    }
  };
</script>

