    </div>
    <!-- <div>这是{{housePrice}}</div> -->
    <div class="label-name">{{$t('size.price')}}</div>
    <div>
      <div v-if="$route.name=='About'">
        <span v-if="list.disabled!=undefined" class="price" v-show="data.size==size" v-for="data in list.size" :key="list.index">0</span>
        <span v-if="list.disabled==undefined" class="price" v-show="data.size==size" v-for="data in list.size" :key="list.index">{{data.house_price | hasPrice}}</span>
      </div>

      <div v-if="$route.name=='Wm'">
        <span class="price" v-show="data.size==size" v-for="data in list.size" :key="list.index">{{data.pack_price | hasPrice}}</span>
      </div>
    </div>
    <div @click="addToCart(list)" class="add" >{{$t("btn.addGift")}}</div>
  </div>
</template>

<script>
