<style scoped>
  div {
    height: auto;
  }



  #select-size {
    box-sizing: border-box;
    width: 8.666666rem;
    height: 13.173333rem;
   
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #FFFFFF;
    z-index: 12;
    padding: 0.533333rem;

    border-radius: 0.32rem;
  }

  .size {
    background-color: white;
  width: 3.573333rem;
