import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
export default new Vuex.Store({
  state: {
   /* expires_in : 0,
    access_token : '',
    refresh_token : '',
    token_type : ''*/
    userInfo : {},  //用户信息
    totalPrice: 0, //购物车总金额
    totalGoods:0, //购物车商品总量
    cartInfo:[],   //购物车商品信息
    shopInfo:{},  //商店信息
    aboutlists:[],  //列表商品信息
    category:[],    //分类标签信息
    cartShow:false,  //购物车是否显示
    bufferInfo:[],   //总的套餐个数
    bufferCategory:[], //套餐标签
    bufferDetail:[],  //套餐列表详情
    disabled:false,  //是否属于套餐
    bufferInCartNums:0,
    telNumber:'',   //电话号码
    captcha:'',   //验证码
    areaCode:'81', //区号
    diningStyle:'堂食',
    orderNote:'',  //订单备注
    nums:{},
    pickSizeGoods:[1],
    pickGood:null,
    pickGood2:null,
    ordersInfo:{},
    tax:0,
    tableId:''
  },
  getters:{

  },
  mutations: {

    //
    setTableId(state,data){
      state.tableId = data
    },
    //用户信息
    userInfo(state,data){
       state.userInfo = data
    },
    //购物车总价格变动
    totalPrice(state,data){
       state.totalPrice += data
    },
    //购物车数量变动
    totalGoods(state){

       // state.totalGoods += data
      state.totalGoods = 0
      setTimeout(()=>{
        for(let i of state.cartInfo){
           if(i.num != 0){
              state.totalGoods += i.num
           }
        }
      },0)


    },
    aboutlists(state,data){
      state.aboutlists = data
    },
    /*about商品列表*/
    setList(state,data){
          // console.log(state.aboutlists.indexOf(data), '在列表的位置')
            /*这是控制可以选择的*/
          if(data.canSelectSize){

              let findGood={}
            for(let i of state.aboutlists){

                  if(typeof(data.id)=='string' && data.id.includes(i.id)  ) {
                      console.log('我要控制这个')

                        state.nums[data.id] = data.num
                        findGood = i
                    }
              }

              console.log(state.nums)
              let sum = 0
              for(let key in state.nums){

                if(key.includes(findGood.id)){
                      sum+=state.nums[key]
                }
              }
              findGood.num = sum
              console.log(findGood)
              let index = state.aboutlists.indexOf(findGood)
              console.log(index)
              state.aboutlists.splice(index,1)
              setTimeout(()=>{
                  state.aboutlists.splice(index,0,findGood)
              },0)

              if(data.num==0){
                console.log('现在是0个,应该从购物车列表移除')
                state.cartInfo.splice(state.cartInfo.indexOf(data),1)
              }
          }
          else{


          let index = state.aboutlists.indexOf(data)

          state.aboutlists.splice(index,1)
          setTimeout(()=>{
            state.aboutlists.splice(index,0,data)
            // console.log('用新的替换他2')
          },0)
            if(data.num==0){
              console.log('现在是0个,应该从购物车列表移除')
              state.cartInfo.splice(state.cartInfo.indexOf(data),1)
            }
          }



    },
    updataBufferrDetail(state,data){

      if(data.canSelectSize){

          let findGood={}
        for(let i of state.bufferDetail){

              if(typeof(data.id)=='string' && data.id.includes(i.id)  ) {
                  console.log('我要控制这个')

                    state.nums[data.id] = data.num
                    findGood = i
                }
          }

          console.log(state.nums)
          let sum = 0
          for(let key in state.nums){

            if(key.includes(findGood.id)){
                  sum+=state.nums[key]
            }
          }
          findGood.num = sum
          console.log(findGood)
          let index = state.bufferDetail.indexOf(findGood)
          console.log(index)
          state.bufferDetail.splice(index,1)
          setTimeout(()=>{
              state.bufferDetail.splice(index,0,findGood)
          },0)

          if(data.num==0){
            console.log('现在是0个,应该从购物车列表移除')
            state.cartInfo.splice(state.cartInfo.indexOf(data),1)
          }
      }else{
              let index = state.bufferDetail.indexOf(data)
              state.bufferDetail.splice(index,1)
              setTimeout(()=>{
                state.bufferDetail.splice(index,0,data)
                // console.log('用新的替换他2')
              },0)
              if(data.num==0){
                console.log('现在是0个,应该从购物车列表移除')
                state.cartInfo.splice(state.cartInfo.indexOf(data),1)
              }
      }



    },
    updataBufferInfo(state,data){
        console.log('执行updataBufferInfo')
      // console.log(state.bufferInfo)
        let index = state.bufferInfo.indexOf(data)
        state.bufferInfo.splice(index,1)
        setTimeout(()=>{
          state.bufferInfo.splice(index,0,data)
          // console.log('用新的替换他2')
        },0)
        if(data.num==0){
          console.log('现在是0个,应该将从套餐购物车列表移除')
          state.cartInfo.splice(state.cartInfo.indexOf(data),1)
          setTimeout(()=>{
            for (let i of state.bufferDetail) {
              if (i.category_id == data.buffet_id) {
                i.disabled = true
                i.num = 0
                   if(state.cartInfo.includes(i)){
                     console.log('从购物车移除了这一项')
                     state.cartInfo.splice(state.cartInfo.indexOf(i),1)
                   }
              }
            }
          },0)

          /*更新套餐列表*/
          if (state.bufferDetail.length != 0) {
            let temp = state.bufferDetail.splice(0, state.bufferDetail.length)
           // console.log(temp,12312321)
            setTimeout(() => {
              state.bufferDetail = [...temp]
            }, 0)
          }
          // let temp = state.
        }
    },

    clearListNum(state){
      for(let list of state.aboutlists){
          list.num = 0
          // console.log(list.num)
      }
      for(let list of state.bufferInfo){
        list.num = 0
      }
      for(let list of state.bufferDetail){
        list.num = 0
      }

    },
    clearCart(state){
      // console.log('成功清空购物车')
        state.totalPrice = 0
        state.totalGoods = 0
        state.cartInfo = []
        state.nums = {}
    },
    setcartInfo(state,data){


      if(data.num!=0){
              console.log('执行了setcartInfo123')
        if(!state.cartInfo.includes(data)){
              // console.log(state.cartInfo.includes(data))
              state.cartInfo.push(data)
              console.log('当前购物车还没有这个，把点击的这个加进去')
        }else{
          // console.log(state.cartInfo.indexOf(data))
          //用新的替换他
          //删除他
          if(data.canSelectSize){
            for(let i of state.cartInfo){
              // console.log(i.id,data.id)
              // console.log(i.selectedSize,data.selectedSize)
              if(i.id == data.id && i.selectedSize == data.selectedSize){
                  console.log('该尺寸的商品已经有了')
                  let index = state.cartInfo.indexOf(data)
                  state.cartInfo.splice(index,1)
                  setTimeout(()=>{
                    state.cartInfo.splice(index,0,data)
                    // console.log('用新的替换他2')
                  },0)
              }else{
                console.log('该尺寸的商品还没有')
              }
            }
          }else{
            console.log('普通商品已经有了')

            let index = state.cartInfo.indexOf(data)
            state.cartInfo.splice(index,1)
            setTimeout(()=>{
              state.cartInfo.splice(index,0,data)
              // console.log('用新的替换他2')
              },0)
            // console.log('用新的替换他')
            }

          }

       }
       else{
            console.log('现在NUM是0，我要清空你')
          if(JSON.stringify(state.cartInfo).includes(JSON.stringify(data)))  {
               console.log('从购物车移除了这一项')
               let index = state.cartInfo.findIndex((i) => {
                 return i.id == data.id
               })
               console.log(index,'找到了')
               state.cartInfo.splice(index,1)
          }else{
              //console.log(data.id,'asaga')
              for(let i of state.cartInfo){
              //  console.log(i.id)
                if(i.id.includes(data.id)){

                  let index = state.cartInfo.indexOf(i)
                   state.cartInfo.splice(index,1)
                }
              }
          }
          // if(state.cartInfo.includes(data)){
          //      console.log('从购物车移除了这一项')
          //      let index = state.cartInfo.indexOf(data)
          //      console.log(index,'找到了')
          //      state.cartInfo.splice(index,1)
          // }else{
          //   console.log('没得这个')
          // }
       }
    },
    updataList(state){
          // for(let list of state.aboutlists){
          //    state.splice()
          // }
         let temp =  state.aboutlists.splice(0,state.aboutlists.length)

          setTimeout(()=>{
              state.aboutlists = [...temp]
          },0)
           console.log('重置普通列表')
    },
    resetBufferInfo(state){
        let temp =  state.bufferInfo.splice(0,state.bufferInfo.length)

         setTimeout(()=>{
             state.bufferInfo = [...temp]
         },10)
          console.log('重置bufferInfo')

    },
    resetBufferDetail(state){
        let temp =  state.bufferDetail.splice(0,state.bufferDetail.length)

         setTimeout(()=>{
             state.bufferDetail = [...temp]
         },10)
          console.log('重置bufferDetail')

    },
    addBufferToCategory(state,data){
            state.category.push(data)
    },
    setBufferCategory(state,data){
        state.bufferCategory.push(data)
    },
    setBufferDetail(state,data){
      state.bufferDetail.push(data)
    },
    setBufferInfo(state,data){
       state.bufferInfo = data
       for(let i of state.bufferInfo){
         i.num = 0,
         i.house_base_price = i.set_price,

         i.max = 0
         i.selectedSize='套餐'
       }
      },
    addImgToBuffer(state){
      for(let i of state.bufferDetail){
        for(let j of state.aboutlists){
          if(i.id.split('for')[0]==j.id){
            i.oss_url = j.oss_url,
            i.description = j.description,
            i.disabled = true
            // console.log(this)
            i.max = 5
            i.size = j.size
          }
        }
      }
    },
    /*设置按钮是否可用*/
    setDisabled(state,data){
        state.disabled = data
    },
    setShopInfo(state,data){
        state.shopInfo = data
    },
    setBufferInCartNums(state,data){
      state.bufferInCartNums += data
      // console.log( state.bufferInCartNums)
    },
    updataTotalGoods(state,data){

    },
    updataTelNumber(state,data){
      state.telNumber = data
    },
    updataCaptcha(state,data){
      state.captcha = data
    },
    updataAreaCode(state,data){
      state.areaCode = data
    },
    updataDiningStyle(state,data){
      state.diningStyle=data
    },
    updataOrderNote(state,data){
      state.orderNote = data
    },
    setOrderInfo(state,data){
    state.ordersInfo = data
    },
    setTax(state,data){
      state.tax = data
    }
  },
  actions: {
      getListInfo(store,shopId){
        axios
          .post("https://jzk80vaqs7.execute-api.ap-northeast-1.amazonaws.com/dev/get_shop_all_info/v1", {
            shop_id: shopId,
          })
          .then((res) => {
            console.log(res.data)

            store.commit('setBufferInfo',res.data.buffet_info)

            store.state.category = res.data.category_data
            // store.state.category.push(...res.data.buffet_info)
            for(let i of res.data.buffet_info){

              store.commit('setBufferCategory',{
                  id:i.buffet_id.toString(),
                  name:i.set_name,

              })

              for(let j of i.buffet_detail){
                // console.log(j)
                store.commit('setBufferDetail', {
                  id:j.products_id.toString()+'for'+i.buffet_id,
                  name:j.products_name,
                  category_id : i.buffet_id,
                  house_base_price : 0,
                })
              }

            }


            store.commit('aboutlists', res.data.products_data)

            for (let i of store.state.aboutlists) {
                i.num = 0
                i.size = eval('(' + i.size + ')')
            //   i.size = i.size.split('},')
            //   i.size = i.size.split(' ')
            //  console.log(i.size,i.category_id,i.size.length)
                if(i.size.length==1){
                  i.selectedSize = i.size [0].size
                  i.canSelectSize = false
                }
                if(i.size.length>1){
                    i.selectedSize = i.size [0].size

                    i.canSelectSize = true
                }
                // console.log(i)
                // console.log(i.size )
                // console.log(i.selectedSize)
            }


            for (let i of store.state.category) {
              i.id = i.id.toString()

            }
            store.commit('addImgToBuffer')

            for (let i of store.state.bufferDetail) {
                  i.num = 0
                  if(i.size.length==1){
                    i.selectedSize = i.size [0].size
                    i.canSelectSize = false
                  }
                  if(i.size.length>1){
                      i.selectedSize = i.size [0].size

                      i.canSelectSize = true
                  }
                // console.log(i)
            }
             // console.log(store.state.bufferInfo)

              /*测试*/
              // let stoarge = res.data.buffet_info
              // console.log('套餐信息',stoarge)
              // for(let i of res.data.buffet_info){
              //   i.buffet_detail[0].price = i.set_price
              //   i.buffet_detail[0].time = i.set_time
              // }
              // console.log('套餐信息',res.data.buffet_info)
             // console.log(store.state.aboutlists)
            store.commit('setShopInfo',res.data.shop_info)
          }).catch(err => {
            console.log(err)
          })
      }

  },
  modules: {
  }
})
